import React, { useState, useEffect } from "react";
import DatePicker from "react-datepicker";
import TimePicker from "react-time-picker";

import AddHabit from "./AddHabit";
import "../../style/HabitTracker.css";
import { getFromLocalStorageWithRange } from "../storage/storageHelpers.js";

function HabitTracker() {
  const [isBtnClicked, setIsBtnClicked] = useState(false);
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [startTime, setStartTime] = useState("10:00");
  const [endTime, setEndTime] = useState("11:00");

  const [selectedDays, setSelectedDays] = useState([]);
  const days = ["월", "화", "수", "목", "금", "토", "일"];
  const handleClick = (day) => {
    // 이미 선택된 요일이면 선택 해제
    if (selectedDays.includes(day)) {
      setSelectedDays(selectedDays.filter((d) => d !== day));
    } else {
      // 새로운 요일이면 선택 추가
      setSelectedDays([...selectedDays, day]);
    }
  };
  return (
    <>
      <div className="HabitTrack">
        <h3>
          습관 추적기(일주일 단위, 한달 단위, 일년 단위 / 나무로 시각화 예정 /
          평일 주말 공휴일 기타 예외 설정 기능도)
        </h3>
        <button onClick={() => setIsBtnClicked(!isBtnClicked)}>
          Add(습관 추가)
        </button>
      </div>
      {isBtnClicked === true ? (
        <div className="AddHabit">
          <div className="StartDate">
            <h3>습관 추적 시작 날짜</h3>
            <DatePicker
              dateFormat="yyyy.MM.dd"
              shouldCloseOnSelect
              minDate={new Date("2000-01-01")}
              todayButton="On Today"
              selected={selectedDate}
              onChange={(date) => setSelectedDate(date)}
            />
          </div>
          <div className="Period">
            <h3>주기 설정</h3>
            <div>
              {days.map((day) => (
                <span
                  key={day}
                  onClick={() => handleClick(day)}
                  style={{
                    // 선택된 요일의 경우에는 글자 색을 진하게
                    fontWeight: selectedDays.includes(day) ? "bold" : "normal",
                    // span 태그 사이에 공백 추가
                    margin: "0 5px",
                    // 마우스 커서를 손가락 모양으로 변경
                    cursor: "pointer",
                  }}
                >
                  {day}
                </span>
              ))}
              <div>
                선택된 요일:{" "}
                {selectedDays.length > 0 ? selectedDays.join(", ") : "없음"}
              </div>
            </div>
          </div>
          <div className="Name">
            <h3>제목</h3>
            <input placeholder="Write your Habit Name"></input>
          </div>{" "}
          <br />
          <span>--- 선택사항 ---</span>
          <div className="Description">
            <h3>상세 정보</h3>
            <input placeholder="Explain your Habit"></input>
          </div>{" "}
          <div className="TimeSelect">
            <h3>시작 시간 / 종료 시간</h3>
            <TimePicker onChange={setStartTime} value={startTime} />
            {" 부터 "}
            <TimePicker onChange={setEndTime} value={endTime} />{" "}
          </div>
          <br />
          <button>저장</button>
        </div>
      ) : (
        <div>{""}</div>
      )}
    </>
  );
}

export default HabitTracker;
